::selection {
    background-color: $clr-bg;
    color: $clr-bg-variant;
}

* {
    box-sizing: border-box;
    margin: 0;
}

html {
    font-family: Roboto, sans-serif;
    line-height: 1.8;
    line-height: 1.5;
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    width: 100%;
    height: 100%;
    background-color: $clr-bg;
    line-height: 1.7;
    font-family: 'Poppins', sans-serif;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
    vertical-align: baseline;
}

img {
    display: block;
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.btn {
    cursor: pointer;
}


button {
    border-radius: 3px;
    cursor: pointer;
    display: inline-block;
    padding: 0.75rem 1.2rem;
    transition: all .4s ease;
    /* width: -webkit-max-content; */
    width: max-content;
}