.home-page {

    .home-page-body {
        height: 70vh;
        padding: 5rem;
        justify-content: space-between;
        z-index: 5;

        .student-section{
            gap: 20px;
            justify-content: center;
            color: #424242;
        }
        .container {
            position: relative;
            z-index: 0;
        }

        .student-btn {
            border: 1px solid #3b99dc;
            background-color: #4db5ff;
            color: #424242;
            height: 70px;
            width: 140px;
            font-size: 17px;
            font-weight: 600;
        }
    }
}